import React from 'react';
import { View, Panel, PanelHeader, Epic, Tabbar, TabbarItem } from '@vkontakte/vkui';
import Icon28HomeOutline from '@vkontakte/icons/dist/28/home_outline';
import Icon28ServicesOutline from '@vkontakte/icons/dist/28/services_outline';
import Icon28UserCircleOutline from '@vkontakte/icons/dist/28/user_circle_outline';
import Icon28MarketOutline from '@vkontakte/icons/dist/28/market_outline';
import Icon28ListOutline from '@vkontakte/icons/dist/28/list_outline';
import '@vkontakte/vkui/dist/vkui.css';

export class Example extends React.Component {
    constructor (props) {
        super(props);

        this.state = {
            activeStory: 'profile'
        };
        this.onStoryChange = this.onStoryChange.bind(this);
    }

    onStoryChange (e) {
        this.setState({ activeStory: e.currentTarget.dataset.story })
    }

    componentDidMount() {

    }

    render () {

        return (
            <Epic activeStory={this.state.activeStory} tabbar={
                <Tabbar>
                    <TabbarItem
                        onClick={this.onStoryChange}
                        selected={this.state.activeStory === 'main'}
                        data-story="main"
                        text="Главная"
                    ><Icon28HomeOutline /></TabbarItem>
                    <TabbarItem
                        onClick={this.onStoryChange}
                        selected={this.state.activeStory === 'catalog'}
                        data-story="catalog"
                        text="Каталог"
                    ><Icon28ServicesOutline/></TabbarItem>
                    <TabbarItem
                        onClick={this.onStoryChange}
                        selected={this.state.activeStory === 'basket'}
                        data-story="basket"
                        label="12"
                        text="Корзина"
                    ><Icon28MarketOutline /></TabbarItem>
                    <TabbarItem
                        onClick={this.onStoryChange}
                        selected={this.state.activeStory === 'profile'}
                        data-story="profile"
                        text="Профиль"
                    ><Icon28UserCircleOutline /></TabbarItem>
                    <TabbarItem
                        onClick={this.onStoryChange}
                        selected={this.state.activeStory === 'more'}
                        data-story="more"
                        text="Ещё"
                    ><Icon28ListOutline /></TabbarItem>
                </Tabbar>
            }>
                <View id="main" activePanel="main">
                    <Panel id="main">
                        <PanelHeader>Главная</PanelHeader>
                    </Panel>
                </View>
                <View id="catalog" activePanel="catalog">
                    <Panel id="catalog">
                        <PanelHeader>Каталог</PanelHeader>
                    </Panel>
                </View>
                <View id="basket" activePanel="basket">
                    <Panel id="basket">
                        <PanelHeader>Корзина</PanelHeader>
                    </Panel>
                </View>
                <View id="profile" activePanel="profile">
                    <Panel id="profile">
                        <PanelHeader>Профиль</PanelHeader>
                    </Panel>
                </View>
                <View id="more" activePanel="more">
                    <Panel id="more">
                        <PanelHeader>Ещё</PanelHeader>
                    </Panel>
                </View>
            </Epic>
        )
    }
}